import { Link } from "react-router-dom";
import Header from "./components/Header";
import { register } from "../../api";
import { useState } from "react";
import { toast } from "react-toastify";
import FormField from "../../components/FormField";
import LoadingButton from "../../components/LoadingButton";

const Register = (props) => {
  const [name, setName] = useState("");
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [passwordRepeat, setPasswordRepeat] = useState("");
  const [errors, setErrors] = useState({});
  const [btnLoading, setBtnLoading] = useState(false);

  const fields = [
    {label: "Nama", value: name, onChange: (e) => setName(e.target.value), error: errors?.name, type: "text"},
    {label: "Username", value: username, onChange: (e) => setUsername(e.target.value), error: errors?.username, type: "text"},
    {label: "Email", value: email, onChange: (e) => setEmail(e.target.value), error: errors?.email, type: "email"},
    {label: "No HP", value: phone, onChange: (e) => setPhone(e.target.value), error: errors?.phone, type: "number"},
    {label: "Password", value: password, onChange: (e) => setPassword(e.target.value), error: errors?.password, type: "password"},
    {label: "Konfirmasi Password", value: passwordRepeat, onChange: (e) => setPasswordRepeat(e.target.value), error: errors?.password_repeat, type: "password"},
  ]

  const handleSubmit = async () => {
    setBtnLoading(true);
    try {
      const res = await register({ name, username, email, phone, password, password_repeat :passwordRepeat });
      localStorage.setItem("userId", res.data.id);
      toast.success("Registrasi berhasil!");
      setBtnLoading(false);
      props.history.push("/user/verification");
    } catch (err) {
      if(err?.response?.status === 400) setErrors(err?.response?.data);
      setBtnLoading(false);
    }
  }

  return (
    <div className="p-4 text-dark" style={{height: '100vh'}}>
      <Header />
      <h5 className="text-primary text-center mb-4">Registration</h5>
      {fields.map((field, i) => (
        <FormField 
          label={field.label}
          value={field.value}
          onChange={field.onChange}
          error={field.error}
          type={field.type}
          key={i}
        />
      ))}
      <div className="form-group">
        {btnLoading ? 
          <LoadingButton />
        : <button className="btn btn-primary btn-sm btn-block" onClick={handleSubmit}>Register</button>
        }
      </div>
      <div className="d-flex justify-content-center mb-4">
        <small style={{fontSize: '.95em'}}>
          Sudah punya akun? <Link to="/auth/login" className="text-primary">Login</Link>
        </small>
      </div>
    </div>
  )
}

export default Register;