import instance from "./instance";

export async function createTransaction(data) {
  return await instance.post("transactions", data);
}

export async function updateTransaction(data) {
  return await instance.put("transactions", data);
}

export async function getTransactions() {
  return await instance.get("transactions");
}

export async function getTransaction(id) {
  return await instance.get(`transactions/${id}`);
}

export async function getTotalTransaction() {
  return await instance.get(`transactions/total-transaction`);
}

export async function deleteTransaction(id) {
  return await instance.delete(`transactions/${id}`);
}