import { FaWallet } from "react-icons/fa";
import Skeleton from "react-loading-skeleton";
import NumberFormat from "react-number-format";

const TransactionByWallet = (props) => {
  const { moneys } = props;
  return (
    <div className="mt-3 shadow-sm bg-light">
      <div className="pl-2 pt-2 pb-1 d-flex text-dark">
        <FaWallet className="mr-1 align-self-center" size="0.85em" />
        <span style={{fontSize: '0.95em'}}>Money in Wallet</span>
      </div>
      <hr className="mx-2 my-0"/>
      <div className="pt-3 pb-2">
        <div className="row">
          {moneys.length === 0 ? [1,2,3,4].map(i => (
            <div className="text-center col-6 mb-3 col-sm-6" key={i}>
              <small className="text-secondary">
                <Skeleton width={100}/>
              </small>
              <span className="d-block mt-0 text-dark">
                <Skeleton width={125} height={25}/>
              </span>
            </div>
          )) : moneys.map(money => (
            <div className="text-center col-6 mb-3 col-sm-6" key={money.id}>
              <small className="text-secondary">{money.name}</small>
              <span className={`d-block ${money.totalMoney < 0 ? "text-danger font-weight-bold" : "text-dark"}`}>
                <small>Rp</small>
                <h5 className={`d-inline`}>
                  <NumberFormat 
                    value={money.totalMoney}
                    displayType={"text"}
                    thousandSeparator={true}
                    renderText={value => value}
                  />
                </h5>
              </span>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}

export default TransactionByWallet;