import Header from "./components/Header";
import { resetMyPassword } from "../../api";
import { useState } from "react";
import { toast } from "react-toastify";
import FormField from "../../components/FormField";
import LoadingButton from "../../components/LoadingButton";

const ResetMyPassword = (props) => {
  const [email, setEmail] = useState("");
  const [btnLoading, setBtnLoading] = useState(false);

  const handleSubmit = async () => {
    if(email === ""){
      toast.error("Email tidak boleh kosong!");
      return;
    }
    setBtnLoading(true);
    try {
      const res = await resetMyPassword({ email });
      if(res.status === 200){
        toast.success(res.data.message);
        localStorage.setItem("email", email);
      }
      setBtnLoading(false);
      props.history.push("/user/new-password");
    } catch (err) {
      if(err?.response?.data) toast.error(err?.response?.data?.message);
    }
    setBtnLoading(false);
  }

  return (
    <div className="p-4 text-dark" style={{height: '100vh'}}>
      <Header />
      <h5 className="text-primary text-center mb-4">Reset My Password</h5>
        <span>Silahkan masukkan email yang terdaftar untuk mereset password</span>
        <FormField 
          label=""
          type="email"
          value={email}
          onChange={e => setEmail(e.target.value)}
        />
      <div className="form-group">
        {btnLoading ? 
          <LoadingButton className="btn btn-outline-danger btn-sm btn-block"/>
        : <button className="btn btn-danger btn-sm btn-block" onClick={handleSubmit}>Reset</button>
        }
        <button className="btn btn-outline-primary btn-sm btn-block" onClick={() => window.history.back()}>Batal</button>
      </div>
    </div>
  )
}

export default ResetMyPassword;