import navs from "./_nav";
import { useLocation, useHistory } from "react-router-dom";

const BottomMenu = (props) => {
  const [location, history] = [useLocation(), useHistory()];
  const iconSize = 28;

  return (
    <div className="bottom-nav d-flex bg-primary text-white d-flex justify-content-around">
      {navs.map((nav, i) => (
        <div 
          className={`w-100 p-2 d-flex justify-content-center cursor-pointer 
                      ${nav.to === location.pathname || (nav.to === `${location.pathname}${location.search}`) ? 
                        "bottom-nav-item-active" : 
                        "bottom-nav-item"}`} 
          key={i} 
          onClick={() => history.push(nav.to)}
        >
          <div className="d-flex flex-column align-items-center">
            <nav.icon size={iconSize}/>
            <small style={{whiteSpace: 'nowrap'}}>{nav.title}</small>
          </div>
        </div>
      ))}
    </div>
  )
}

export default BottomMenu;