import { HashRouter, Redirect, Route, Switch } from "react-router-dom";
import routes from "./routes";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function App() {
  const userdata = JSON.parse(localStorage.getItem("userdata")) || false;
  const token = localStorage.getItem("token") || false
  return (
    <>
      <HashRouter>
        <Switch>
          {routes.map((route, i) => (
            route.public ? (
              <Route 
                path={route.path} 
                component={route.component} 
                exact={route.exact} 
                key={i}   
              />
            ) : userdata && token ? (
              <Route 
                path={route.path} 
                component={route.component} 
                exact={route.exact} 
                key={i}   
              />
            ) : <Redirect to="/auth/login" key={i} />
          ))}
        </Switch>
      </HashRouter>
      <ToastContainer
        position="top-right"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      >
      </ToastContainer>
    </>
  );
}

export default App;