import { FaUserCircle } from "react-icons/fa";
import Container from "../../containers/Container";
import { MdKeyboardArrowRight } from "react-icons/md";

const MyProfile = (props) => {
  const userdata = JSON.parse(localStorage.getItem("userdata"));

  const logout = () => {
    localStorage.clear();
    window.location.href = "/cashflow/";
  }

  return (
    <Container icon={FaUserCircle} title="My Profile">
      <div className="main-content">
        <div className="bg-light p-3 shadow-sm d-flex align-items-center">
          <FaUserCircle size={48} className="text-dark mr-2" />
          <div className="d-flex flex-column">
            <span>{userdata?.name}</span>
            <small>{userdata?.email}</small>
          </div>
        </div>
        <div className="mt-4 text-dark">
          <div className="d-flex justify-content-between border-bottom cursor-pointer py-2 mb-2 align-items-center">
            <span className="d-block">Edit Profile</span>
            <MdKeyboardArrowRight />
          </div>
          <div className="d-flex justify-content-between border-bottom cursor-pointer py-2 mb-2 align-items-center">
            <span className="d-block">Ganti Password</span>
            <MdKeyboardArrowRight />
          </div>
          <div className="d-flex justify-content-between border-bottom cursor-pointer py-2 mb-2 align-items-center" onClick={logout}>
            <span className="d-block text-danger">Keluar</span>
            <MdKeyboardArrowRight />
          </div>
        </div>
      </div>
    </Container>
  )
}

export default MyProfile;