import axios from "axios";
import { toast } from "react-toastify";

const instance = axios.create({
  // baseURL: 'http://localhost:8080/api/'
  baseURL: 'https://ci4-cashflow.herokuapp.com/api'
});


instance.interceptors.request.use((request) => {
  const token = localStorage.getItem("token");
  request.headers["Authorization"] = `Bearer ${token}`;
  return request;
});

instance.interceptors.response.use((response) => response, function (error) {
  if(error.response.status === 500 || error.response.status === 401 || error.response.status === 400) {
    toast.error(error?.response?.data?.message);
    if(error.response.status === 401){
      localStorage.clear();
      window.location.href = "/cashflow/"
    }
  }
  return Promise.reject(error);
});

export default instance;