import { useEffect, useState } from "react";
import { MdKeyboardArrowRight, MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";
import NavTop from "../../components/NavTop";
import LoadingButton from "../../components/LoadingButton";
import { createTransaction } from "../../api";
import NumberFormat from "react-number-format";

const TransactionCreate = (props) => {
  const [amount, setAmount] = useState("");
  const [date, setDate] = useState("");
  const [notes, setNotes] = useState("");
  const [transactionType, setTransactionType] = useState(0);
  const walletName = localStorage.getItem("walletName");
  const walletId = localStorage.getItem("walletId");
  const [errors, setErrors] = useState({});
  const [btnLoading, setBtnLoading] = useState(false);

  const handleSubmit = async () => {
    setBtnLoading(true);
    try {
      await createTransaction({amount, date, notes, type: transactionType, wallet_id: walletId});
      window.history.back();
    } catch (err){
      setErrors(err?.response?.data);
      setBtnLoading(false);
    }
  }

  const showError = (error) => {
    return <small className="text-danger d-block mt-1" >{error}</small>
  }

  useEffect(() => {
    return () => {
      localStorage.removeItem("walletName");
      localStorage.removeItem("walletId");
    }
  }, []);

  return (
    <>
      <NavTop title="Create Transaction" />
      <div className="main-content">
        <div className="form-group">
          <label>Pilih Wallet</label>
          <div 
            className="form-control form-control-sm cursor-pointer align-items-center d-flex justify-content-between" 
            onClick={() => props.history.push("/wallet?type=select")}
          >
            <span>{walletName || ""}</span>
            <MdKeyboardArrowRight />
          </div>
          {showError(errors?.wallet_id)}
        </div>
        <div className="form-group">
          <label>Total Transaksi</label>
          <NumberFormat
            className="form-control form-control-sm"
            placeholder="Rp. "
            thousandSeparator={true}
            prefix={"Rp. "}
            onValueChange={e => setAmount(e.floatValue)}
            value={amount}
          />
          {showError(errors?.amount)}
        </div>
        <div className="form-group">
          <label>Tanggal</label>
          <input 
            type="date" 
            className="form-control form-control-sm"
            value={date}
            onChange={e => setDate(e.target.value)}  
          />
          {showError(errors?.date)}
        </div>
        <div className="form-group">
          <label>Jenis Transaksi</label>
          <div className="d-flex">
            <div className="p-2 bg-light d-flex align-items-center shadow-sm cursor-pointer text-dark mr-2" onClick={() => setTransactionType(0)} >
              {transactionType === 0 ? 
                <MdRadioButtonChecked /> :
              <MdRadioButtonUnchecked />}
              <small style={{fontSize: '.8em'}} className="ml-2">Incoming Transaction</small>
            </div>
            <div className="p-2 bg-light d-flex align-items-center shadow-sm cursor-pointer text-dark" onClick={() => setTransactionType(1)} >
              {transactionType === 1 ?
                <MdRadioButtonChecked /> :
              <MdRadioButtonUnchecked />}
              <small style={{fontSize: '.8em'}} className="ml-2">Outcoming Transaction</small>
            </div>
          </div>
          {showError(errors?.type)}
        </div>
        <div className="form-group">
          <label>Catatan</label>
          <textarea 
            rows="5" 
            className="form-control form-control-sm" 
            style={{resize: 'none'}}
            value={notes}
            onChange={e => setNotes(e.target.value)}
          ></textarea>
          {showError(errors?.notes)}
        </div>
        <div className="form-group">
          {btnLoading ? <LoadingButton /> :
            <button className="btn btn-primary btn-block btn-sm" onClick={handleSubmit}>Save</button>
          }
        </div>
      </div>
    </>

  )
}

export default TransactionCreate;