import { useEffect, useState } from "react";
import { FaHome } from "react-icons/fa";
import { getTotalMoney, getTotalTransaction } from "../../api";
import Container from "../../containers/Container";
import TotalMoney from "./components/TotalMoney";
import TransactionByWallet from "./components/TransactionByWallet";
import TransactionHistory from "./components/TransactionHistory";

const Home = (props) => {
  const [totalTrx, setTotalTrx] = useState({});
  const [moneyInWallet, setMoneyInWallet] = useState([]);

  const loadData = async () => {
    try {
      const res1 = await getTotalTransaction();
      setTotalTrx(res1.data);
      const res2 = await getTotalMoney();
      setMoneyInWallet(res2.data);
    } catch (err) {}
  }

  useEffect(() => {
    loadData();
  }, []);

  return (
    <>
      <Container icon={FaHome} title="Home">
        <div className="main-content">
          <TotalMoney total={totalTrx} />
          <TransactionByWallet moneys={moneyInWallet} />
          <TransactionHistory />
        </div>
      </Container>
    </>
  );
}

export default Home;