import { FaTimes } from "react-icons/fa";
import LoadingButton from "./LoadingButton";

const Dialog = (props) => {
  const { onPositive, onNegative, onCLose, show, loading } = props;
  const className = show ? 'dialog-bg' : 'd-none';
  return (
    <div className={className}>
      <div className="dialog-content">
        <div className="dialog-title">
          <span>Konfirmasi</span>
          <FaTimes className="cursor-pointer" onClick={onCLose ? onCLose : onNegative}/>
        </div>
        <div className="dialog-body">
          Apakah anda yakin akan menghapus data?
        </div>
        <div className="d-flex justify-content-end">
          <button className="btn btn-outline-primary py-1 px-3 btn-sm" onClick={onNegative}>Tidak</button>
          {loading ? <LoadingButton className="btn btn-danger ml-2 py-1 px-3 btn-sm" /> :
            <button className="btn btn-danger ml-2 py-1 px-3 btn-sm" onClick={onPositive}>Ya</button>
          }
        </div>
      </div>
    </div>
  )
}

export default Dialog;