import qs from "qs";
import { useEffect, useState } from "react";
import { Redirect } from "react-router-dom";
import { getWallets } from "../../api";
import WalletList from "./WalletList";
import WalletSelect from "./WalletSelect";

const Wallet = (props) => {
  const wallet = qs.parse(props.location.search, {ignoreQueryPrefix: true});
  const [data, setData] = useState([]);
  const [loadingApi, setLoadingApi] = useState(true);

  const loadData = async () => {
    setLoadingApi(true);
    try {
      const res = await getWallets();
      setData(res.data);
    } catch (err){}
    setLoadingApi(false);
  }

  useEffect(() => loadData(), []);

  if(wallet.type === "list") {
    return <WalletList {...props} wallets={data} loadingApi={loadingApi} />
  } else if (wallet.type === "select") {
    return <WalletSelect {...props} wallets={data} loadingApi={loadingApi} />
  } else return <Redirect to="/" />
}

export default Wallet;