import NumberFormat from "react-number-format";
import Skeleton from "react-loading-skeleton";

const TotalMoney = (props) => {
  const { total } = props;
  return (
    <div className="py-3 px-2 shadow-sm bg-light">
      <div className="d-flex justify-content-around">
        <div className="w-50 text-center">
          <small className="text-secondary">Incoming Transaction</small>
          <span className="d-block mt-1 text-dark">
            {!total.incoming ? <Skeleton width={125} height={20.5} /> :
              <>
                <small>Rp</small>
                <h5 className="d-inline">
                  <NumberFormat
                    value={total.incoming}
                    displayType={"text"}
                    thousandSeparator={true}
                    renderText={value => value}
                  />
                </h5>
              </>
            }
          </span>
        </div>
        <div className="w-50 text-center">
          <small className="text-secondary">Outcoming Transaction</small>
          <span className="d-block mt-1 text-dark">
            {!total.outcoming ? <Skeleton width={125} height={20.5} /> :
              <>
                <small>Rp</small>
                <h5 className="d-inline">
                  <NumberFormat
                    value={total.outcoming}
                    displayType={"text"}
                    thousandSeparator={true}
                    renderText={value => value}
                  />
                </h5>
              </>
            }
          </span>
        </div>
      </div>
    </div>
  )
}

export default TotalMoney;