import { FaShoppingCart } from "react-icons/fa";
import Container from "../../containers/Container";
import FabPlus from "../../components/FabPlus";
import Dialog from "../../components/Dialog";
import EmptyState from "../../components/EmptyState";
import TransactionCard from "./components/TransactionCard";
import { deleteTransaction, getTransactions } from "../../api";
import { useEffect, useState } from "react";
import SkeletonLoading from "./components/SkeletonLoading";

const Transaction = (props) => {
  const [transactions, setTransactions] = useState([]);
  const [loadingApi, setLoadingApi] = useState(true);
  const [dialog, setDialog] = useState(false);
  const [btnLoading, setBtnLoading] = useState(false);
  const [trxId, setTrxId] = useState("");

  const loadData = async () => {
    setLoadingApi(true);
    try {
      const res = await getTransactions();
      setTransactions(res.data);
    } catch (err){}
    setLoadingApi(false);
  }

  const handleDelete = async () => {
    setBtnLoading(true);
    try {
      await deleteTransaction(trxId);
      setDialog(false);
      await loadData();
    } catch (err){}
    setBtnLoading(false);
  }

  useEffect(() => {
    loadData();
  }, []);

  return (
    <Container icon={FaShoppingCart} title="Transaction">
      <div className="main-content">
        <input 
          type="search" 
          className="form-control form-control-sm" 
          placeholder="search..."  
        />
        <hr className="my-2"/>
        {loadingApi ? [1,2].map(i => <SkeletonLoading key={i} />) : 
          transactions.length === 0 ? <EmptyState /> : 
          transactions.map(transaction => (
            <TransactionCard 
              {...props} 
              key={transaction.id} 
              transaction={transaction} 
              onDelete={() => {setDialog(true); setTrxId(transaction.id)}}
              onEdit={() => props.history.push(`/transaction/edit?id=${transaction.id}`)}
            />
        ))}
      </div>
      <FabPlus 
        onClick={() => props.history.push("/transaction/create")}
      />
      <Dialog 
        show={dialog}
        onNegative={() => setDialog(false)}
        onPositive={handleDelete}
        loading={btnLoading}
      />
    </Container>
  )
}

export default Transaction;