import { HiArrowRight } from "react-icons/hi";

const CardList = (props) => {
  const { wallet } = props;
  return (
    <div 
      className="bg-light p-3 shadow-sm d-flex justify-content-between cursor-pointer mb-2" 
      onClick={() => props.history.push(`/wallet/edit?id=${wallet.id}`)}
    >
      <div>
        <span className="d-block text-dark" style={{fontSize: '.95em'}}>{wallet.name}</span>
        <small className="d-block text-secondary" style={{fontSize: '.85em'}}>{wallet.description}</small>
      </div>
      <div className="d-flex align-items-center text-secondary">
        <HiArrowRight />
      </div>
    </div>
  )
}

export default CardList;