import Header from "./components/Header";
import { userVerification } from "../../api";
import { useState } from "react";
import { toast } from "react-toastify";
import FormField from "../../components/FormField";
import LoadingButton from "../../components/LoadingButton";

const UserVerification = (props) => {
  const userId = localStorage.getItem("userId") || "";
  const [code, setCode] = useState("");
  const [btnLoading, setBtnLoading] = useState(false);

  const handleSubmit = async () => {
    setBtnLoading(true);
    try {
      const res = await userVerification({ user_id: userId, code });
      if(res.status === 200){
        toast.success(res.data.message);
        localStorage.removeItem("userId");
      }
      setBtnLoading(false);
      props.history.push("/auth/login");
    } catch (err) {
      setBtnLoading(false);
    }
  }

  return (
    <div className="p-4 text-dark" style={{height: '100vh'}}>
      <Header />
      <h5 className="text-primary text-center mb-4">Account Activation</h5>
        <span>Silahkan masukkan kode yang sudah di kirim melalui email untuk konfirmasi. Kode hanya berlaku 5 menit.</span>
        <FormField 
          label=""
          type="number"
          value={code}
          onChange={e => setCode(e.target.value)}
        />
      <div className="form-group">
        {btnLoading ? 
          <LoadingButton />
        : <button className="btn btn-primary btn-sm btn-block" onClick={handleSubmit}>Konfirmasi</button>
        }
      </div>
    </div>
  )
}

export default UserVerification;