import { useEffect, useState } from "react";
import qs from "qs";
import NavTop from "../../components/NavTop";
import LoadingButton from "../../components/LoadingButton";
import { deleteWallet, getWallet, updateWallet } from "../../api";
import FormField from "../../components/FormField";
import Dialog from "../../components/Dialog";

const WalletEdit = (props) => {
  const param = qs.parse(props.location.search, {ignoreQueryPrefix: true});
  const id = param.id;
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [btnLoading, setBtnLoading] = useState(null);
  const [errors, setErrors] = useState({});
  const [dialog, setDialog] = useState(false);

  const handleDelete = async () => {
    setBtnLoading("delete");
    try {
      await deleteWallet(id);
      window.history.back();
    } catch (err){
      setDialog(false);
      setBtnLoading(null);
    }
  }
  
  const handleSubmit = async () => {
    setBtnLoading("save");
    try {
      await updateWallet({name, description, id});
      window.history.back();
    } catch (err) {
      setErrors(err?.response?.data);
      setBtnLoading(null);
    }
  }
  
  useEffect(() => {
    const loadData = async () => {
      try {
        const res = await getWallet(id);
        setName(res.data.name);
        setDescription(res.data.description);
      } catch (err){}
    }
    loadData();
  }, [id]);

  return (
    <>
      <NavTop title="Edit Wallet" />
      <div className="main-content">
        <FormField 
          label="Name"
          value={name}
          onChange={e => setName(e.target.value)}
          type="text"
          error={errors?.name}
        />
        <div className="form-group">
          <label>Description</label>
          <textarea 
            name="description" 
            className="form-control form-control-sm" 
            rows="3" 
            style={{resize: 'none'}}
            value={description}
            onChange={e => setDescription(e.target.value)}
          ></textarea>
          <small className="text-danger d-block mt-1">{errors?.description}</small>
        </div>
        <div className="form-group">
          {btnLoading === "save" ? 
            <LoadingButton /> :
            <button className="btn btn-primary btn-block btn-sm" onClick={handleSubmit}>Save</button>
          }
          <button className="btn btn-outline-danger btn-block btn-sm" onClick={() => setDialog(true)}>Delete</button>
        </div>
      </div>
      <Dialog 
        show={dialog}
        onNegative={() => setDialog(false)}
        onPositive={handleDelete}
        loading={btnLoading === "delete"}
      />
    </>
  )
}

export default WalletEdit;