import Header from "./components/Header";
import { renewMyPassword } from "../../api";
import { useState } from "react";
import { toast } from "react-toastify";
import FormField from "../../components/FormField";
import LoadingButton from "../../components/LoadingButton";

const NewPassword = (props) => {
  const [newPassword, setNewPassword] = useState("");
  const [newPasswordRepeat, setNewPasswordRepeat] = useState("");
  const [btnLoading, setBtnLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const email = localStorage.getItem("email");

  const handleSubmit = async () => {
    setBtnLoading(true);
    try {
      const res = await renewMyPassword({ new_password: newPassword, new_password_repeat: newPasswordRepeat, email });
      if(res.status === 200){
        toast.success(res.data.message);
        localStorage.removeItem("email");
      }
      setBtnLoading(false);
      props.history.push("/auth/login");
    } catch (err) {
      setErrors(err?.response?.data);
      toast.error(err?.response?.data?.message);
    }
    setBtnLoading(false);
  }

  return (
    <div className="p-4 text-dark" style={{height: '100vh'}}>
      <Header />
      <h5 className="text-primary text-center mb-4">New Password</h5>
        <FormField 
          label="Password Baru"
          type="password"
          value={newPassword}
          onChange={e => setNewPassword(e.target.value)}
          error={errors?.new_password}
        />
        <FormField 
          label="Konfirmasi Password Baru"
          type="password"
          value={newPasswordRepeat}
          onChange={e => setNewPasswordRepeat(e.target.value)}
          error={errors?.new_password_repeat}
        />
      <div className="form-group">
        {btnLoading ? 
          <LoadingButton/>
        : <button className="btn btn-primary btn-sm btn-block" onClick={handleSubmit}>Simpan</button>
        }
      </div>
    </div>
  )
}

export default NewPassword;