import NavTop from "../../components/NavTop";
import { createWallet } from "../../api";
import { useState } from "react";
import FormField from "../../components/FormField";
import LoadingButton from "../../components/LoadingButton";

const WalletCreate = (props) => {
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [btnLoading, setBtnLoading] = useState(false);
  const [errors, setErrors] = useState({});

  const handleSubmit = async () => {
    setBtnLoading(true);
    try {
      await createWallet({name, description});
      window.history.back();
    } catch (err){
      setErrors(err?.response?.data);
      setBtnLoading(false)
    }
  }

  return (
    <>
      <NavTop title="Create Wallet" />
      <div className="main-content">
        <FormField 
          label="Name"
          value={name}
          onChange={e => setName(e.target.value)}
          type="text"
          error={errors?.name}
        />
        <div className="form-group">
          <label>Description</label>
          <textarea 
            name="description" 
            className="form-control form-control-sm" 
            rows="3" 
            style={{resize: 'none'}}
            value={description}
            onChange={e => setDescription(e.target.value)}
          ></textarea>
          <small className="text-danger d-block mt-1">{errors?.description}</small>
        </div>
        <div className="form-group">
          {btnLoading ? <LoadingButton /> :
            <button className="btn btn-primary btn-block btn-sm" onClick={handleSubmit}>Save</button>
          }
        </div>
      </div>
    </>
  )
}

export default WalletCreate;