import { FaWallet } from "react-icons/fa";
import EmptyState from "../../components/EmptyState";
import FabPlus from "../../components/FabPlus";
import Container from "../../containers/Container";
import CardList from "./components/CardList";
import SkeletonLoading from "./components/SkeletonLoading";

const WalletList = (props) => {
  const { wallets, loadingApi } = props;
  return (
    <Container icon={FaWallet} title="Wallet">
      <div className="main-content">
        <input 
          type="search" 
          className="form-control form-control-sm" 
          placeholder="search..."  
        />
        <hr className="my-2"/>
        {loadingApi ? [1,2,3,4,5].map(i => <SkeletonLoading key={i} />) : 
          wallets.length === 0 ? <EmptyState /> :
          wallets.map(wallet => (
          <CardList key={wallet.id} {...props} wallet={wallet} />
        ))}
        <FabPlus 
          onClick={() => props.history.push("/wallet/create")}
        />
      </div>
    </Container>
  )
}

export default WalletList;