import { Link } from "react-router-dom";
import Header from "./components/Header";
import { login } from "../../api";
import { useState } from "react";
import FormField from "../../components/FormField";
import LoadingButton from "../../components/LoadingButton";

const Register = (props) => {
  const [identifier, setIdentifier] = useState("");
  const [password, setPassword] = useState("");
  const [errors, setErrors] = useState({});
  const [btnLoading, setBtnLoading] = useState(false);

  const fields = [
    {label: "Username / Email", value: identifier, onChange: (e) => setIdentifier(e.target.value), error: errors?.identifier, type: "text"},
    {label: "Password", value: password, onChange: (e) => setPassword(e.target.value), error: errors?.password, type: "password"},
  ]

  const handleSubmit = async () => {
    setBtnLoading(true);
    try {
      const res = await login({ identifier, password });
      localStorage.setItem("userdata", JSON.stringify(res.data.userdata));
      localStorage.setItem("token", res.data.token);
      setTimeout(() => {
        window.location.href = "/cashflow/";
      }, 5000);
    } catch (err) {
      if(err?.response?.status === 400) {
        setErrors(err?.response?.data);
      }
      setBtnLoading(false);
    }
  }

  return (
    <div className="p-4 text-dark" style={{height: '100vh'}}>
      <Header />
      <h5 className="text-primary text-center mb-4">Login</h5>
      {fields.map((field, i) => (
        <FormField 
          label={field.label}
          value={field.value}
          onChange={field.onChange}
          error={field.error}
          type={field.type}
          key={i}
        />
      ))}
      <div className="form-group">
        {btnLoading ? 
          <LoadingButton />
        : <button className="btn btn-primary btn-sm btn-block" onClick={handleSubmit}>Login</button>
        }
      </div>
      <div className="d-flex justify-content-between">
        <small style={{fontSize: '.95em'}}>
          <Link to="/auth/register" className="text-primary">Register</Link>
        </small>
        <small style={{fontSize: '.95em'}}>
          <Link to="/user/reset-my-password" className="text-primary">Reset My Password</Link>
        </small>
      </div>
    </div>
  )
}

export default Register;