import { FaRegCalendarAlt, FaWallet, FaPen, FaTrashAlt } from "react-icons/fa";
import NumberFormat from "react-number-format";

const TransactionCard = (props) => {
  const { transaction, onEdit, onDelete } = props;
  const type = ["Incoming Transaction", "Outcoming Transaction"]
  return (
    <div className="p-3 bg-light mb-3 shadow-sm">
      <div className="d-flex justify-content-between">
        <span className="d-flex text-dark">
          <FaRegCalendarAlt size=".85em" className="d-flex mr-1 align-self-center" />
          <small className="d-flex align-self-center" >{transaction.date}</small>
        </span>
        <span className="d-flex text-dark">
          <small className="d-flex align-self-center mr-1">{transaction?.wallet?.name}</small>
          <FaWallet size=".85em" className="d-flex align-self-center" />
        </span>
      </div>
      <hr className="my-3" />
      <div className="text-center py-3 text-dark">
        <span className="mb-2 d-block">
          <small className="d-inline">Rp</small>
          <h3 className="d-inline">
            <NumberFormat
              value={transaction.amount}
              displayType={"text"}
              thousandSeparator={true}
              renderText={value => value}
            />
          </h3>
        </span>
        <span className="d-block blockquote-footer">
          {transaction.notes || (<i>tidak ada catatan.</i>)}
        </span>
        <small className="d-block mt-1 text-dark">({type[transaction.type]})</small>
      </div>
      <hr className="my-3" />
      <div className="d-flex justify-content-start">
        <span className="d-flex">
          <span className="d-flex mr-2 cursor-pointer btn-edit" onClick={onEdit}>
            <FaPen size=".85em" className="d-flex mr-1 align-self-center" />
            <small className="d-flex align-self-center">Edit</small>
          </span>
          <span className="d-flex cursor-pointer btn-delete" onClick={onDelete}>
            <FaTrashAlt size=".85em" className="d-flex mr-1 align-self-center" />
            <small className="d-flex align-self-center">Hapus</small>
          </span>
        </span>
      </div>
    </div>
  )
}

export default TransactionCard;